* {
  margin: 0px;
  padding: 0px;
  border: 0px;

  text-align: center;
  font-family: 'Space Grotesk', Helvetica, sans-serif; 
}

body > #root > .App > .interface
{
  min-width: 100vw;
  min-height: 100vh;
}

.interface
{
  display: flex;
  flex-direction: column;
}

.navbar
{
  width: 100%;
}

.nav
{
  margin: 0rem 1rem;
}

.mains
{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.showstuff
{
  margin: 3vh 0px;
}

.features-list
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.ft-title
{
  margin-right: 50%;
}

.features
{
  flex-grow: 1;
  margin-top: 2vh;
  margin-right: 5vh;
}